import { Input, Button, Spin, Row, Col, Typography, Image, Card, message } from 'antd';
import { UserOutlined, LockOutlined, LoadingOutlined } from '@ant-design/icons';
import { useState } from 'react';
import service from '../../services/user';
import util from '../../utils/util';

export default function Login() {
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [ajxRequesting, setAjxRequesting] = useState(false);

    const login = () => {
        setAjxRequesting(true);
        service.login({ email: email, password: password }).then(res => {
            message.success(res.message);
            util.setUserData(res.data);
            window.location.reload();
        }).catch((err) => {
            if (typeof err.message !== 'string') {
                message.error(err.message[Object.keys(err.message)[0]][0]);
            } else {
                message.error(err.message);
            }
        }).finally(() => {
            setAjxRequesting(false);
        });
    }

    return (
        <>
            <Row justify="space-around" align="top" style={{ backgroundColor: "#fa680900", backgroundImage: "url('https://www.allkeyedup.com.au/wp-content/uploads/login-bg.png')", height: '100vh', paddingTop: 60, backgroundRepeat: 'no-repeat' }}>
                <Col style={{ backgroundColor: 'white', padding: '50px 0px' }}
                    xs={{ span: 22 }}
                    lg={{ span: 14 }}
                    xl={{ span: 10 }}
                >
                    <Row gutter={[60, 60]} align="middle" justify="space-around">
                        <Col>
                            <Image src="https://i.pinimg.com/736x/f2/be/0a/f2be0a43cdf09b025bef223dfc68d822.jpg" preview={false} width={'200px'} />
                        </Col>
                        <Col>
                            <Spin spinning={ajxRequesting} indicator={<LoadingOutlined style={{ fontSize: 24 }} spin />}>
                                <form onSubmit={e => { e.preventDefault(); }} autoComplete="off" spellCheck={false}>
                                    <Card hoverable={true} bodyStyle={{ width: 280 }}>
                                        <Typography.Title level={3} style={{ textAlign: 'center' }}>Welcome</Typography.Title>
                                        <Input
                                            style={{ margin: '10px 0px' }}
                                            size="middle"
                                            prefix={<UserOutlined />}
                                            value={email}
                                            onChange={(e) => { setEmail(e.target.value) }}
                                            placeholder="Admin Email" />
                                        <Input.Password
                                            style={{ margin: '10px 0px' }}
                                            size="middle"
                                            prefix={<LockOutlined />}
                                            value={password}
                                            onChange={(e) => { setPassword(e.target.value) }}
                                            placeholder="Password" />
                                        <Button block type="primary" htmlType="submit" onClick={() => login()} style={{ margin: '10px 0px' }}>Login</Button>
                                    </Card>
                                </form>
                            </Spin>
                        </Col>
                    </Row>
                </Col>
            </Row>
        </>
    );
};